import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class ProviderService {

  constructor() {}
  saveTemplate(temp) {
    localStorage.setItem('templates', JSON.stringify(temp));
  }
  getTemplate() {
    return localStorage.getItem('templateBody');
  }
  getAllTemplate() {
    return JSON.parse(localStorage.getItem('templates'));
  }
  getAllQuestion() {
    return JSON.parse(localStorage.getItem('allQuestion'));
  }
  saveToAllQuestion(item) {
    let temp = [];
    temp = this.getAllQuestion() || [];
    temp.unshift(item);
    localStorage.setItem('allQuestion', JSON.stringify(temp));

  }
  deleteTemplate(id) {
    let all = [];
      all = this.getAllTemplate();
    for (let i = 0; i < all.length; i++) {
      if (all[i].id == id) {
        all.splice(i, 1);
      }
    }
    this.saveTemplate(all);
  }
  getemplateById(id) {
    let elem = {};
    console.log(id)
    let all = this.getAllTemplate();
    all.forEach(el => {
      if (el.id == id) {
        elem = el;
        console.log('sssssssssssssssss')
      }
    });
    return elem;
  }
  getCurenttemplate() {
    return JSON.parse(localStorage.getItem('curentTemplateBody'));
  }
  getCurentQuestions() {
    return JSON.parse(localStorage.getItem('question'));
  }
  getAllBilets() {
    return JSON.parse(localStorage.getItem('allBilets')) || [];
  }
  saveBilets(obj) {
    let all = this.getAllBilets();
    obj['id'] = all.length;
    all.unshift(obj);
    localStorage.setItem('allBilets', JSON.stringify(all));
  }
  deleteOneBilet(id) {
    let all = this.getAllBilets();
    for (let i = 0; i < all.length; i++) {
      if (all[i].id == id) {
        all.splice(i, 1);
      }
    }
    localStorage.setItem('allBilets', JSON.stringify(all));
  }
  getOneBilet(id) {
    let all = this.getAllBilets();
    all.forEach(el => {
      if (el.id == id) {
        return el;
      }
    });
    return null;
  }
}
