import {Component, ElementRef, OnInit, Pipe, ViewChild} from '@angular/core';
import {ProviderService} from '../provider.service';
import {Router} from '@angular/router';
import {DomSanitizer} from '@angular/platform-browser';
import {timeout} from 'q';

@Component({
  selector: 'app-final-confirm',
  templateUrl: './final-confirm.component.html',
  styleUrls: ['./final-confirm.component.scss']
})
export class FinalConfirmComponent implements OnInit {
public templateTop;
public templateBottom;
public name;
public saved = false;
public descr;
public questions;
public Biletlength = +JSON.parse(localStorage.getItem('bilet-number'));
  private elementRef: ElementRef;
  @ViewChild('result') result: ElementRef;
  constructor(
    private router: Router,
    private provider: ProviderService
  ) {
  }
  ngOnInit() {
    this.templateTop = this.provider.getCurenttemplate().top || '';
    this.templateBottom = this.provider.getCurenttemplate().bottom || '';
    this.questions = this.provider.getCurentQuestions() || [];
    this.createStyleTag();
    this.pasteStyles();
  }

  createRange(n) {
    var items: number[] = [];
    for ( let i = 1; i <= n; i++) {
      items.push(i);
    }
    return items;
  }
  createStyleTag() {
    if (!document.querySelector('.customStyles')) {
      let node = document.createElement('style');
      node.className = 'customStyles';
      document.querySelector('#customDiv').appendChild(node);
    }
  }
  pasteStyles() {
    document.querySelector('.customStyles').innerHTML = this.provider.getCurenttemplate().styles;
  }
  saveBilets() {
    let a = {
      'questions': this.questions,
      'htmlTop': this.templateTop,
      'htmlBottom': this.templateBottom,
      'name': this.name,
      'descr': this.descr,
      'styles': this.provider.getCurenttemplate().styles
    };
    this.saved = true;
    window.scrollTo(0, 0);
    this.provider.saveBilets(a);
    setTimeout(() => {
      this.saved = false;
    }, 1000);
  }
}

