import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {InputTextComponent} from './input-text/input-text.component';
import {CardsListComponent} from './cards-list/cards-list.component';
import {CreateTemplateComponent} from './create-template/create-template.component';
import {MainPageComponent} from './main-page/main-page.component';
import {ChooseTemplateComponent} from './choose-template/choose-template.component';
import {FinalConfirmComponent} from './final-confirm/final-confirm.component';
import {EditTemplateComponent} from './edit-template/edit-template.component';
import {StandartTemplateComponent} from './standart-template/standart-template.component';
import {TemplateListComponent} from './template-list/template-list.component';

const routes: Routes = [
  {
    path: '',
    component: CardsListComponent
  },
  {
    path: 'question',
    component: InputTextComponent
  },
  {
    path: 'list',
    component: CardsListComponent
  },
  {
    path: 'templates-list',
    component: TemplateListComponent
  },
  {
    path: 'templ',
    component: CreateTemplateComponent
  },
  {
    path: 'choose-template',
    component: ChooseTemplateComponent
  },
  {
    path: 'final',
    component: FinalConfirmComponent
  },
  {
    path: 'edit-template/:id',
    component: EditTemplateComponent
  },
  {
    path: 'template/standard',
    component: StandartTemplateComponent
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
